// Auth module global SCSS file

:root {
    --grey: #999;
    --blue: #39f;
    --orange-100: #bd6f34;
    --orange-tint-90: #c9783a;
    --orange-tint-80: #cd8249;
    --orange-tint-70: #d18d58;
    --orange-tint-60: #d59767;
    --orange-tint-50: #daa277;
    --orange-tint-40: #deac86;
    --orange-tint-30: #e2b695;
    --orange-tint-20: #e6c1a4;
    --orange-tint-10: #eacbb3;
    --orange-tint-8: #eed5c2;
    --orange-tint-6: #f3e0d2;
    --orange-tint-4: #f7eae1;
    --orange-tint-2: #fbf5f0;
    --white: #fff;
    --orange-shade-100: #bd6f34;
    --orange-shade-90: #af6730;
    --orange-shade-80: #a15f2c;
    --orange-shade-70: #945728;
    --orange-shade-60: #864f25;
    --orange-shade-50: #794721;
    --orange-shade-40: #794721;
    --orange-shade-30: #5e371a;
    --orange-shade-20: #512f16;
    --orange-shade-10: #432812;
    --orange-shade-8: #36200f;
    --orange-shade-6: #28180b;
    --orange-shade-4: #1b1007;
    --orange-shade-2: #0d0804;
    --black: #000;
    --off-white: #ede7d8;
    --navy: #354253;
    --green: #27ae60;
    --yellow: #f2c94c;
    --red: #eb5757;
    --font-sans-serif: 'Mulish', sans-serif;
    --font-serif: 'Times New Roman', Times, Baskerville, Georgia, serif;
    --base-background-color: var(--orange-tint-2);
    --base-font-family: var(--font-sans-serif);
    --base-font-size: 1.6rem;
    --base-line-height: 1.5;
    --base-ls: 0.2px;
    --base-text-color: var(--black);
    --base-min-width: 320px;
    --base-transition-speed: 0.2s;
    --base-link-color: var(--orange-100);
    --heading-font: 'Montserrat', sans-serif;
    --headings-sizes-h1: 4rem;
    --headings-sizes-h2: 2.7rem;
    --headings-sizes-h3: 2.4rem;
    --headings-sizes-h4: 2.1rem;
    --headings-sizes-h5: 1.7rem;
    --headings-sizes-h6: 1.5rem;
    --input-bg: var(--orange-tint-2);
    --input-color: var(--navy);
    --input-fz: 1.4rem;
    --input-radius: 0.4rem;
    --input-padding: 1.1rem 1.2rem;
    --input-height: 4.2rem;
    --header-height: 12.8rem;
    --header-bg: var(--white);
    --header-color: var(--orange-shade-10);
    --header-padding: 1.2rem calc(var(--header-height) + 2rem);
    --headings-font-family: var(--font-sans-serif);
}

.error-text {
    color: var(--red);
    font-size: 12px;
    display: block;
    text-align: left;
}

.clearfix:after {
    content: '';
    display: block;
    clear: both;
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
}

.ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--base-text-color);
    background: var(--base-background-color);
    font: var(--base-font-size) / var(--base-line-height)
        var(--base-font-family);
    min-width: var(--base-min-width);
    letter-spacing: var(--base-ls);
}

img {
    max-width: 100%;
    height: auto;
}

.gm-style img {
    max-width: none;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-left {
    text-align: left;
}

