h2,
.h2 {
    font-size: var(--headings-sizes-h2);
}

h3,
.h3 {
    font-size: var(--headings-sizes-h3);
}

h4,
.h4 {
    font-size: var(--headings-sizes-h4);
}

h5,
.h5 {
    font-size: var(--headings-sizes-h5);
}

h6,
.h6 {
    font-size: var(--headings-sizes-h6);
}

p {
    margin: 0;
}

a {
    color: var(--base-link-color);
    text-decoration: none;
}

a:focus {
    text-decoration: none;
}

small {
    font-size: 1.2rem;
    line-height: 1.8rem;
}

.icon {
    width: 100%;
    height: 100%;
    fill: currentColor;
    stroke: currentColor;
}

.green {
    color: var(--green);
}

.yellow {
    color: var(--yellow);
}

form,
fieldset {
    margin: 0;
    padding: 0;
    border-style: none;
    border-radius: 0.8rem;
}

form small,
fieldset small {
    //font-weight: 600;
    display: block;
}

form small:not(:last-child),
fieldset small:not(:last-child) {
    margin-bottom: 0.8rem;
}

label {
    font-size: 1.4rem;
    color: var(--input-color);
}

