select {
    /* Firefox */
    -webkit-appearance: none;
    /* Safari and Chrome */
    appearance: none;
    display: flex;
    align-items: center;
    align-content: center;
    z-index: 0;
    box-sizing: border-box;
    font-family: var(--base-font-family);
    font-size: var(--input-fz);
    font-weight: 400;
    border: 0.1rem solid var(--input-bg);
    border-radius: var(--input-radius);
    padding: var(--input-padding);
    height: var(--input-height);
    outline: none;
    background: url('../../assets/images/icons/icon-down.svg'), var(--input-bg);
    background-repeat: no-repeat;
    background-position: center right 0.5rem;
    background-size: 1rem 0.5rem;
    cursor: pointer;
}

textarea {
    resize: none;
    vertical-align: top;
    min-height: 10rem;
}

button[type='submit'],
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
    -webkit-appearance: none;
    appearance: none;
    cursor: pointer;
    font-family: var(--form-font-family);
    background-color: var(--orange-100);
    color: var(--white);
    border-radius: 0.33em;
}

button[type='submit'][disabled],
input[type='button'][disabled],
input[type='reset'][disabled],
input[type='file'][disabled],
input[type='submit'][disabled] {
    background-color: var(--orange-tint-20);
    cursor: not-allowed;
}

@keyframes rippleEffect {
    0% {
        transform: scale(1);
    }

    100% {
        opacity: 0;
        transform: scale(var(--scale));
    }
}

.form {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.form-helper {
    position: relative;
    display: block;
    padding: 0 2.1rem !important;
    font-weight: 600;
    color: var(--black);
}

.form-helper:not(:last-child) {
    margin-bottom: 2rem;
}

.form__brands {
    position: relative;
    justify-content: center;
    max-width: 100% !important;
    background-color: transparent !important;
}

.form__brands > * {
    background-color: var(--white);
    min-height: 100%;
    padding: 2.4rem;
    border-radius: 0.8rem;
    margin-bottom: 0.8rem;
    width: 100%;
}

.form__brands-left {
    max-width: 40.1rem;
}

.form__brands-left:not(:last-child) {
    margin-right: 0.8rem;
}

.form__brands-right {
    max-width: 59.2rem;
}

.form__brands h3:not(:last-child) {
    margin-bottom: 1.6rem;
}

.form__brands .email-message-box {
    position: relative;
    letter-spacing: 0.4px;
    padding: 1.4rem 1.6rem;
    border: 0.1rem solid #e0e0e0;
    font-size: 1.4rem;
    outline: none;
}

.form__brand {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    border: 0.1rem solid var(--orange-tint-10);
    padding: 0.7rem 0.8rem 0.7rem 1.6rem;
    font-size: 1.4rem;
    font-weight: 400;
}

.form__brand-email {
    flex: 1;
}

.form__brand .remove-email {
    position: relative;
    z-index: 1;
    cursor: pointer;
    min-width: 2.4rem;
    height: 2.4rem;
    width: 2.4rem;
    color: var(--orange-100);
}

.form__group {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -0.9rem;
}

.form__group--checkboxes {
    margin-bottom: 2rem;
}

.form__group > * {
    padding: 0 0.9rem;
}

.form__field {
    position: relative;
    width: 100%;
    margin-bottom: 1.6rem;
}

.form__field--half {
    width: calc(50% - 0.9rem);
}

.form__field--floating-label {
    padding-top: 2.6rem;
}

.form__field--floating-label label {
    position: absolute;
    opacity: 0.64;
    left: 0;
    top: 0;
    transform: translate(1.2rem, 3.6rem);
    transition: all var(--base-transition-speed) ease-in-out;
    pointer-events: none;
}

//.form__field--floating-label input::placeholder,
//.form__field--floating-label textarea::placeholder {
//    color: transparent;
//    opacity: 0;
//}

.form__field--floating-label input:focus ~ label,
.form__field--floating-label input:not(:placeholder-shown) ~ label,
.form__field--floating-label textarea:focus ~ label,
.form__field--floating-label textarea:not(:placeholder-shown) ~ label {
    opacity: 1;
    transform: translate(0, 0);
}

.form__field--floating-label input:-webkit-autofill ~ label,
.form__field--floating-label textarea:-webkit-autofill ~ label {
    opacity: 1;
    transform: translate(0, 0);
}

.form__field.helpers {
    padding-top: 0.9rem;
}

.form__field.helpers:last-child {
    margin-bottom: 0;
}

.form__field.helpers .forgot,
.form__field.helpers .later {
    //text-align: center;
    font-size: 1.4rem;
}

.form__field-help {
    position: relative;
    width: 100%;
    opacity: 0.87;
    padding: var(--input-padding);
}

.form__field.buttons {
    padding-top: 0.9rem;
}

.form__field.buttons:last-child {
    margin-bottom: 0;
}

.form__field.buttons button {
    width: 100%;
    min-height: 48px;
}

.password-show {
    position: absolute;
    right: 1.2rem;
    top: 1.5rem;
    width: 2.2rem;
    height: 2.2rem;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    background-color: currentColor;
    -webkit-mask-image: url('../../assets/images/icons/icon-hidden.svg');
    mask-image: url('../../assets/images/icons/icon-hidden.svg');
    cursor: pointer;
    outline: none;
    z-index: 1;
    color: var(--navy);
    opacity: 0.64;
}

.password-show.active {
    opacity: 1;
    -webkit-mask-image: url('../../assets/images/icons/icon-visible.svg');
    mask-image: url('../../assets/images/icons/icon-visible.svg');
}

.password-message {
    position: absolute;
    display: none;
    right: 6rem;
    height: 2rem;
    bottom: 1.4rem;
    text-align: right;
    z-index: 2;
    opacity: 0.64;
    color: var(--orange-shade-100);
}

.password-message.show {
    display: block;
}

.form__socials {
    position: relative;
    display: block;
    width: 100%;
}

.form__socials .icon {
    position: relative;
    display: inline-block;
    width: 2.4rem;
    height: 2.4rem;
}

.form__socials .icon:not(:last-child) {
    margin-right: 1rem;
}

.form__social {
    position: relative;
    display: flex;
    width: 100%;
    margin-bottom: 0.8rem;
}

.form__social-label {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 4rem;
    min-width: 4rem;
    height: 4rem;
    background-color: var(--orange-tint-10);
    color: var(--white);
    border-top-left-radius: var(--input-radius);
    border-bottom-left-radius: var(--input-radius);
}

.form__social-input {
    flex: 1;
}

.form__social label {
    order: 1;
    transition: all var(--base-transition-speed) ease;
}

.form__social select {
    order: 2;
    height: 4rem;
    color: rgba(53, 66, 83, 0.64);
}

.form__social select {
    border: none;
}

.form__social input {
    order: 3;
    height: 4rem;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
}

.form__social input:focus ~ label,
.form__social input:focus:required:invalid ~ label,
.form__social input.invalid ~ label,
.form__social input.valid ~ label {
    background-color: var(--orange-100);
}

.form__social input:required:valid ~ label {
    background-color: var(--orange-100);
}

.form__social-add {
    position: relative;
    width: 100%;
}

.form__social-add .button {
    height: 4rem;
    width: 100%;
    cursor: pointer;
    font-size: 1.4rem;
    font-weight: 400;
}

.form__social-add .button .icon,
.form__social-add .button .txt {
    position: relative;
    transition: all var(--base-transition-speed) ease;
    opacity: 0.64;
}

.form__social-add .button:hover .icon,
.form__social-add .button:hover .txt {
    opacity: 1;
}

.form__social-add .icon {
    position: relative;
    display: inline-block;
    width: 2.4rem;
    height: 2.4rem;
}

.form__social-add .icon:not(:last-child) {
    margin-right: 2rem;
}

.checkboxes {
    position: relative;
    display: grid;
    grid-template-columns: repeat(var(--i), 1fr);
    grid-column-gap: 0.6rem;
    grid-row-gap: 0.6rem;
}

.checkboxes:not(:first-child) {
    margin-top: 0.8rem;
}

.checkboxes--bordered .checkbox {
    padding: 0;
}

.checkboxes--bordered .checkbox-text {
    padding: 1.3rem 1.2rem 1.1rem 4rem !important;
    border: 0.1rem solid var(--orange-tint-20);
    color: var(--navy);
}

.checkboxes--bordered .checkbox-text:before {
    left: 1.2rem !important;
}

.checkboxes--bordered .checkbox input:checked ~ .checkbox-text {
    border-color: var(--orange-100);
}

.checkboxes--bordered .checkbox:hover .checkbox-text {
    background-color: var(--orange-tint-2);
}

.checkbox {
    position: relative;
    padding: 0.9rem 0 0 0;
}

.checkbox input {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
}

.checkbox input:checked ~ .checkbox-text:before {
    background-color: var(--orange-100);
    -webkit-mask-image: url('../../assets/images/icons/checked-true.svg');
    mask-image: url('../../assets/images/icons/checked-true.svg');
}

.checkbox label {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    font-weight: 600;
    height: 100%;
}

.checkbox label .checkbox-text {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    height: 100%;
    width: 100%;
    padding-left: 3.3rem;
}

.checkbox label .checkbox-text:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width: 1.8rem;
    height: 1.8rem;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    transition: all 0.15s linear;
    background-color: var(--orange-tint-30);
    -webkit-mask-image: url('../../assets/images/icons/checked-false.svg');
    mask-image: url('../../assets/images/icons/checked-false.svg');
}

.checkbox label:hover {
    cursor: pointer;
}

.checkbox label:hover .radio-text:before {
    background-color: var(--orange-100);
}

.radios {
    position: relative;
    display: grid;
    grid-template-columns: repeat(var(--i), 1fr);
    grid-column-gap: 0.6rem;
    grid-row-gap: 0.6rem;
}

.radios:not(:first-child) {
    margin-top: 0.8rem;
}

.radios--bordered .radio {
    padding: 0;
}

.radios--bordered .radio-text {
    padding: 1.3rem 1.2rem 1.1rem 4rem !important;
    border: 0.1rem solid var(--orange-tint-20);
    color: rgba(53, 66, 83, 0.64);
}

.radios--bordered .radio-text:before {
    left: 1.2rem !important;
}

.radios--bordered .radio input:checked ~ .radio-text {
    border-color: var(--orange-100);
}

.radios--bordered .radio:hover .radio-text {
    background-color: var(--orange-tint-2);
}

.radio {
    position: relative;
    padding: 1.2rem 0;
    color: var(--navy);
}

.radio input {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
}

.radio input:checked ~ .radio-text:before {
    background-color: var(--orange-100);
    -webkit-mask-image: url('../../assets/images/icons/radio--true.svg');
    mask-image: url('../../assets/images/icons/radio--true.svg');
}

.radio label {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    font-weight: 600;
}

.radio label .radio-text {
    position: relative;
    width: 100%;
    padding-left: 3.3rem;
    transition: all 0.15s linear;
}

.radio label .radio-text:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width: 1.8rem;
    height: 1.8rem;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    transition: all 0.15s linear;
    background-color: var(--orange-tint-30);
    -webkit-mask-image: url('../../assets/images/icons/radio--false.svg');
    mask-image: url('../../assets/images/icons/radio--false.svg');
}

.radio label:hover {
    cursor: pointer;
}

.radio label:hover .radio-text:before {
    background-color: var(--orange-100);
}

.button {
    position: relative;
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    border-radius: 0.8rem;
    font-size: 1.8rem;
    font-weight: 700;
    text-decoration: none;
    outline: none;
    border: none;
    padding: 1.5rem 3rem;
}

.button-text,
.button-icon {
    position: relative;
    z-index: 1;
}

.button-navy {
    background-color: var(--navy);
    color: var(--white);
}

.button-primary {
    background-color: var(--orange-100);
    color: var(--white);
}

.button-light {
    background-color: var(--orange-tint-2);
    color: var(--navy);
}

.wv {
    width: 2px;
    height: 2px;
    position: absolute;
    z-index: 1;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.5);
    animation: rippleEffect 0.5s ease-in-out;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.content_auth {
    flex: 1 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
    padding-top: var(--header-height);
}

.section_auth {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding: 3.2rem 4rem;
}

.auth_section {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding: 3.2rem 4rem;
}

.container {
    position: relative;
    width: 100%;
    margin: 0 auto;
    max-width: 130rem;
}

/*Retailer*/
.retailer-page .header {
    box-shadow: none;
}

.header_auth {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 10;
    width: 100%;
    height: var(--header-height);
    background-color: var(--header-bg);
    box-shadow: 0 0.1rem 0 rgba(0, 0, 0, 0.12);
    padding: var(--header-padding);
    display: flex;
    align-items: flex-end;
    align-content: flex-end;
    color: var(--header-color);
}

.header__logo {
    position: absolute;
    right: 0;
    top: 0;
    height: var(--header-height);
    width: calc(var(--header-height) + 2rem);
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.header__logo picture {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
}

.header__logo img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.subheader {
    position: relative;
    width: 100%;
    background-color: var(--header-bg);
    box-shadow: 0 0.1rem 0 var(--orange-tint-10);
    padding: 1.6rem 4rem;
}

.text-message {
    position: relative;
    width: 100%;
    color: var(--orange-shade-20);
}

.info-window {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    max-width: 57.6rem;
    width: 100%;
    background-color: var(--white);
    padding: 1.6rem;
    box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.12);
    border-radius: 0.4rem;
    color: var(--orange-shade-20);
}

.info-window p,
.info-window li {
    font-size: 1.4rem;
    line-height: 1.8rem;
}

.info-window__text {
    position: relative;
    flex: 1;
    padding-right: 2.4rem;
}

.info-window__icon {
    position: relative;
    align-self: flex-start;
    width: 2.4rem;
}

.info-window__icon .icon {
    width: 2.4rem;
    height: 2.4rem;
}

.info-bar {
    position: relative;
    width: 100%;
    background-color: var(--header-bg);
    padding: 1.6rem 4.8rem;
}

.info-bar-container {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.info-bar__item {
    position: relative;
    display: flex;
    font-weight: 600;
    color: var(--navy);
}

.info-bar__item:not(:last-child) {
    margin-right: 2.4rem;
}

.info-bar__item:nth-child(2n) {
    color: var(--orange-100);
}

.info-bar__icon {
    min-width: 2.4rem;
    width: 2.4rem;
    height: 2.4rem;
    margin-right: 1.2rem;
}

.steps {
    position: relative;
    display: flex;
    justify-content: space-between;
}

.steps .step {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-align: center;
    padding: 0.7rem 2.8rem;
    height: 3.4rem;
    border-radius: 10rem;
    border: 0.1rem solid var(--orange-tint-20);
    color: var(--orange-100);
    font-size: 1.2rem;
    line-height: 1.4rem;
    font-weight: 700;
    width: calc((100% - 4 * 6rem) / 5);
}

.steps .step:not(:last-child):after {
    content: '';
    position: absolute;
    width: 6rem;
    height: 0.1rem;
    background-color: var(--orange-tint-20);
    right: -6rem;
    top: 0;
    bottom: 0;
    margin: auto 0;
}

.steps .step.active {
    color: var(--white);
    background-color: var(--orange-100);
    border-color: var(--orange-100);
}

.steps .step.undone:before {
    content: '';
    position: absolute;
    right: 1.6rem;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width: 0.8rem;
    height: 0.8rem;
    border-radius: 100%;
    background-color: var(--red);
}

.steps .step.process:before {
    content: '';
    position: absolute;
    right: 1.6rem;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width: 0.8rem;
    height: 0.8rem;
    border-radius: 100%;
    background-color: var(--white);
}

.steps .step.done {
    border-color: var(--orange-100);
}

.steps .step.done:before {
    content: '';
    position: absolute;
    right: 1.6rem;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width: 1.2rem;
    height: 1.2rem;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    background-color: var(--green);
    -webkit-mask-image: url('../../assets/images/icons/icon-check.svg');
    mask-image: url('../../assets/images/icons/icon-check.svg');
}

.steps .step.done:last-child {
    background-color: var(--orange-100);
    color: var(--white);
}

.steps .step.done:last-child:before {
    background-color: var(--white);
}

.categories {
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(11.7rem, 1fr));
    grid-column-gap: 1.6rem;
    grid-row-gap: 1.6rem;
    margin-bottom: 2.4rem;
}

.categories .category {
    position: relative;
    height: 13.2rem;
    display: block;
    text-align: center;
}

.categories .category input {
    opacity: 0;
    width: 0;
    height: 0;
    position: absolute;
}

.categories .category input:checked + label {
    background-color: var(--orange-tint-70);
    color: var(--white);
}

.categories .category input:checked + label .category__icon {
    opacity: 1;
    color: var(--white);
}

.categories .category label {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    padding: 2.4rem 2rem;
    background-color: var(--white);
    border-radius: 0.4rem;
    color: var(--navy);
    font-size: 1.2rem;
    line-height: 1.4rem;
    font-weight: 400;
    cursor: pointer;
    transition: all 0.2s ease;
}

.categories .category__icon {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 5.6rem;
    height: 5.6rem;
    margin: 0 auto 0.8rem auto;
    opacity: 0.54;
    color: var(--black);
}

.values {
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(18.4rem, 1fr));
    grid-column-gap: 1.6rem;
    grid-row-gap: 1.6rem;
    margin-bottom: 2.4rem;
}

.values .value {
    position: relative;
    height: 11.8rem;
    display: block;
    text-align: center;
}

.values .value input {
    opacity: 0;
    width: 0;
    height: 0;
    position: absolute;
}

.values .value input:checked + label {
    background-color: var(--orange-tint-70);
    color: var(--white);
}

.values .value input:checked + label .value__icon {
    opacity: 1;
    color: var(--white);
}

.values .value label {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    padding: 2.4rem 2rem;
    background-color: var(--white);
    border-radius: 0.4rem;
    color: var(--navy);
    font-size: 1.2rem;
    line-height: 1.4rem;
    font-weight: 400;
    cursor: pointer;
    transition: all 0.2s ease;
}

.values .value__icon {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 5.6rem;
    height: 5.6rem;
    margin: 0 auto 0.8rem auto;
    opacity: 0.54;
    color: var(--black);
}

.sign__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
}

.sign__content > *:not(:last-child) {
    margin-bottom: 2.4rem;
}

.sign__form {
    position: relative;
    width: 100%;
    max-width: 57.6rem;
    background-color: var(--white);
    padding: 24px 24px 32px;
}

.primary__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
}

.primary__content > *:not(:last-child) {
    margin-bottom: 2.4rem;
}

.primary__content .button {
    width: 100%;
}

.primary__text {
    position: relative;
    width: 100%;
    max-width: 57.6rem;
    padding: 2.4rem;
}

.primary__form {
    position: relative;
    width: 100%;
    max-width: 57.6rem;
    background-color: var(--white);
    padding: 2.4rem;
}

.primary__form--wide {
    max-width: 100%;
    padding: 2.4rem 0;
}

.primary__form.no-bg {
    background-color: transparent;
}

@media (max-width: 1160px) {
    .form__brands-left {
        max-width: 59.2rem;
    }

    .form__brands-left:not(:last-child) {
        margin-right: 0;
    }
}

@media (hover: hover) and (pointer: fine),
    (-ms-high-contrast: none),
    (-ms-high-contrast: active) {
    a:hover {
        text-decoration: none;
    }
}
